import React, { useEffect, useRef, useState } from 'react'
import './style.less'
export default function LoadMore(props) {
    const more = useRef()
    const [loadTop, setLoadTop] = useState(10000)
    //1.0 
    // function fandou() {
    //     let timer=null;
    //     return (callback) => {
    //         clearTimeout(timer)
    //         timer=setTimeout(() => {
    //             callback()
    //         }, 1000);
    //     }
    // }
    // const Func=fandou()

    // useEffect   
    useEffect(() => {
        //2.0 
        let timer = null
        // 
        let winHeight = document.documentElement.clientHeight
        // getBoundingClientRectgetBoundingClientRectDOM
        // window.addEventListener('scroll', () => {
        //     if (more.current) {
        //         const moreHeight = more.current.getBoundingClientRect().top
        //         setLoadTop(moreHeight)
        //         // //2.0 
        //         if (timer) {
        //             clearTimeout(timer)
        //         }else{
        //             timer = setTimeout(() => {
        //                 if (winHeight > loadTop) {
        //                    props.onLoadMore()
        //                 }
        //             }, 1000);
        //         }
        //         //1.0 
        //         //   console.log(winHeight,moreHeight);
        //         // if(winHeight>moreHeight) {
        //         //     Func(() => {  
        //         //         console.log(111);
        //         //         props.onLoadMore() })

        //         // }
        //     }
        // })
        window.addEventListener('scroll', scorllHandle)
        function scorllHandle() {
            if (more.current) {
                const moreHeight = more.current.getBoundingClientRect().top
                setLoadTop(moreHeight)
                // //2.0 
                if (timer) {
                    clearTimeout(timer)
                } else {
                    timer = setTimeout(() => {
                        if (winHeight > loadTop) {
                            props.onLoadMore()
                            
                        }
                    }, 1000);
                }
            }
        }
        //   
        return () => {
            window.removeEventListener('scroll',scorllHandle)
            // clearTimeout(timer)
        }
    }, [loadTop])

    return (
        <div className='load' ref={more}>...</div>
    )
}
