import React ,{useState,useEffect}from 'react'
import './style.less'
import {useNavigate,Link} from 'react-router-dom';
import {useDispatch} from 'react-redux'
import {setCollect,removeCollect} from "../../../redux/actions/collect"
export default function BuyAndStoreView(props) {
    const dispatch = useDispatch();
    const navigate=useNavigate()
    const [isCollect,setIsCollect] = useState(false)
    function storeHandle() {
         //  
      
        if (props.user.token!='') {
             /**
             * 
             *  1. 
             *  2. 
             */
              if(isStore()){
                setIsCollect(false)
                // 
                dispatch(removeCollect(props.id))
                alert("")
               
            }else{
                setIsCollect(true)
                // 
                dispatch(setCollect(props.id));
                alert("")
            }
          
        }else{
            // 
            navigate("/login")
        }
    }
        //     
        useEffect(() => { 
            setIsCollect(isStore())
         },[])
    /**
     * 
     * return boolean
     *     true:
     *     false:
     */

function isStore() {
    let collect=props.collect;
    let id=props.id
    return collect.some(item=>{
        return item==id
    })
}


    return (
        <div className="buy-store-container clear-fix">
            <div className="item-container float-left">
                {
                    isCollect?  <button className="selected o" onClick={storeHandle}></button>:
                    <button className="selected" onClick={storeHandle}></button> 
                   
                }
               
               
            </div>

            <div className="item-container float-right">
                <Link to={"/shopcart"}>
                    <button className="selected"></button>
                </Link>
               
            </div>
        </div >
    )
}
